.flyout {
	display:flex;
	flex-direction: column;
	min-height:100vh;
	justify-content: space-between;
}

.home-feature-box .fa {
	font-size:6rem;
}

.home-feature-box span {
	display: block;
	color:#111;
	font-weight:bold;
	margin-top:1.5rem;
}

.example-components-list li > a {
  color: #495057;
}

.example-components-list li:last-child > a {
	border-bottom:0;
}

.example-components-list li > a .fa {
	color:rgba(0,0,0,.35);
	float:right;
}

#cardimage .card {
	background-image: url("./assets/city.jpg");
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center center;
	height: 50vh;
	width: 100%; 
}

#image1 {
	background-image: url("./assets/city.jpg");
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center center;
	height: '100%';
}

#imagecity {
	background-image: url("./assets/city.jpg");
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center center;

}

#sectionimage .view {
	background-image: url("./assets/phoenix.jpg");
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center center;
	height: 100vh;
}

#classicformpage .view {
	background-color: black;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center center;
	min-height: 100vh;
}

#classicformpage .gradient {
background: -webkit-linear-gradient(
	45deg,
	rgba(0, 0, 0, 0.7),
	rgba(72, 15, 144, 0.4) 100%
);
background: -webkit-gradient(
	linear,
	45deg,
	from(rgba(0, 0, 0, 0.7), rgba(72, 15, 144, 0.4) 100%)
);
background: linear-gradient(
	45deg,
	rgba(0, 0, 0, 0.7),
	rgba(72, 15, 144, 0.4) 100%
);
}

#classicformpage .card {
background-color: rgba(126, 123, 215, 0.2);
}

#classicformpage h6 {
line-height: 1.7;
}

#classicformpage .navbar {
transition: background 0.5s ease-in-out, padding 0.5s ease-in-out;
}

#classicformpage .top-nav-collapse {
background: #424f95 !important;
}


@media (max-width: 768px) {
#classicformpage .navbar:not(.top-nav-collapse) {
	background: #424f95 !important;
}
}


#classicformpage label {
color: #fff !important;
}


